@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap);
.header {
  padding: 15px 23px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #fff;
  border-bottom: 1px solid #ebebeb;
}
.left {
  display: flex;
  justify-content: center;
  align-items: center;
}
.left > div {
  margin-right: 20px;
}

.logo img {
  width: 141px;
  height: 41px;
}

.time {
  font-family: "Lato", sans-serif;
}

.time h6 {
  font-size: 11px;
  font-weight: bold;
}

.time p {
  font-size: 11px;
}

.right {
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact {
  text-decoration: none;
  color: #000;
  font-size: 14px;
  font-weight: 900;
  font-family: Avenir;
  margin-right: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact span {
  text-decoration: underline;
  margin-left: 9px;
  line-height: 1;
}

@media (max-width: 576px) {
  .header .left {
    flex-basis: 70%;
  }
  .left {
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
  }
  .header .time {
    flex-basis: 100%;
  }
  .left > .live-wrapper {
  }
  .left > div {
    margin-right: 10px;
  }
}

@media (max-width: 480px) {
  .header {
    padding: 10px 15px;
  }
  .right {
    flex-direction: column;
  }
  .right .contact {
    order: 2;
    margin: 10px 0 0 0;
  }
}

.live {
  font-size: 8px;
  border: none;
  color: #ffffff;
  display: inline-block;
  background-color: #cb444a;
  font-family: "Lato", sans-serif;
  width: 36px;
  height: 18px;
  border-radius: 6px;
  line-height: 18px;
  text-align: center;
  cursor: pointer;
  line-height: 1;
  border: 1px solid #fff;
  font-weight: bold;
}
.live:focus {
  outline: none;
}

.live:hover {
  background: #fff;
  color: #cb444a;
  border: 1px solid #cb444a;
}

.prime-btn {
  background-color: #44cb7c;
  padding: 14px 23px;
  font-size: 15px;
  border: none;
  border-radius: 6px;
  text-decoration: none;
  color: #fff;
  font-family: Lato;
  display: inline-block;
  transition: all 0.3s;
  border: 2px solid transparent;
}

.prime-btn:hover {
  background: #fff;
  color: #44cb7c;
  border: 2px solid #44cb7c;
}

@media (max-width: 1024px) {
  .prime-btn {
    padding: 12px 23px;
    font-size: 14px;
  }
}
@media (max-width: 576px) {
  .btn-wrapper {
    margin: 15px 0;
  }
  .prime-btn {
    padding: 10px 20px;
    font-size: 14px;
    border: 1px solid transparent;
  }
  .prime-btn:hover {
    border: 1px solid #44cb7c;
  }
}

.sidebar {
  background: #fff;
  position: relative;
  background: #fff;
  position: absolute;
  width: 70px;
  padding: 18px 0;
  flex-basis: 70px;
  max-width: 70px;
  left: 0;
  bottom: 0;
  top: 0;
}

.sidebar-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 31px;
  padding: 0 18px;
  text-decoration: none;
  color: #000;
  border-left: 2px solid transparent;
}

.sidebar-item.active {
  border-left: 2px solid #e31837;
}
.sidebar-item:hover {
  border-left: 2px solid #e31837;
}

.sidebar-item img {
  width: 17px;
  height: 18px;
  object-fit: contain;
  margin-bottom: 7px;
}
.sidebar-item span {
  font-family: Avenir;
  font-size: 8px;
}

.sidebar .collaps-btn {
  position: absolute;
  height: 25px;
  width: 25px;
  background: #6b75ca;
  font-size: 18px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 45px;
  right: 0;
  transform: translate(100%, -50%);
  transform-origin: right center;
  opacity: 0.3;
  transition: all 0.3s;
  cursor: pointer;
  display: none;
}

.sidebar:hover .collaps-btn {
  opacity: 1;
}

@media (max-width: 767px) {
  .sidebar {
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.09);
  }
  .sidebar .collaps-btn {
    display: flex;
  }
}

.layout {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  position: relative;
}

.layout > div {
  flex: 1 1;
}
.layout .main {
  margin-left: 70px;
}

@media (max-width: 767px) {
  .layout {
    flex-wrap: wrap;
  }
  .layout .main {
    margin: 0;
  }
}

@media (max-width: 576px) {
}

.aside {
  max-width: 358px;
  background: #fff;
  position: relative;
}

.more {
  margin: 16px 0px 36px 0px;
  margin-left: 92px;
}

.more a {
  font-family: Avenir;
  font-size: 14px;
  font-weight: 900;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #b9191a;
}

.collection .collection-title {
  font-family: Avenir;
  font-size: 14px;
  font-weight: 900;
  padding: 5px 10px;
  background: #cccccc;
}

.aside .collaps-btn {
  position: absolute;
  height: 25px;
  width: 25px;
  background: #6b75ca;
  font-size: 18px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 45px;
  transform: translate(-100%, -50%);
  transform-origin: right center;
  opacity: 0.3;
  transition: all 0.3s;
  cursor: pointer;
}

.aside:hover .collaps-btn {
  opacity: 1;
}

@media (max-width: 1200px) {
  .aside {
    max-width: 300px;
  }
}
@media (max-width: 1024px) {
  .aside {
    max-width: 275px;
  }
  .card-img img {
    height: 46px !important;
    width: 46px !important;
  }
}
@media (max-width: 991px) {
  .aside {
    flex: 1 1;
    position: absolute;
    right: 0;
    height: 100vh;
    box-shadow: -5px 5px 5px rgba(0, 0, 0, 0.09);
  }
}

@media (max-width: 767px) {
  .aside {
    position: relative;
    right: auto;
    height: auto;
    box-shadow: none;
    max-width: none;
    max-width: initial;
    flex-basis: 100% !important;
  }
  .aside .collaps-btn {
    display: none;
  }
}

.search {
  padding: 12px 13px;
  border-radius: 5px;
  border: solid 1px #e0e6ef;
  background-color: #ffffff;
  margin: 20px 17px 0;
  width: calc(100% - 34px);
}
.search:focus {
  outline: none;
}
.search::-webkit-input-placeholder {
  color: #a7a7a7;
  font-size: 12px;
}
.search:-ms-input-placeholder {
  color: #a7a7a7;
  font-size: 12px;
}
.search::-ms-input-placeholder {
  color: #a7a7a7;
  font-size: 12px;
}
.search::placeholder {
  color: #a7a7a7;
  font-size: 12px;
}

.search-wrapper .filter {
  font-family: Avenir;
  font-size: 11px;
  letter-spacing: 0.46px;
  text-align: right;
  display: inline-block;
  padding: 11px 17px 11px 0;
  width: 100%;
  text-decoration: none;
  color: #000;
}

.tab {
  font-family: Avenir;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 2px solid #eaedf3;
}

.tab span {
  font-size: 14px;
  font-weight: 900;
  line-height: 0.71;
  color: rgba(50, 62, 74, 0.62);
  display: inline-block;
  padding: 18px;
  flex: 1 1;
  text-align: center;
  cursor: pointer;
  position: relative;
}

.tab span:first-child::after {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  height: 22px;
  width: 2px;
  background: #eaedf3;
}

.tab span.active {
  color: #323e4a;
}

.accord {
  font-family: Avenir;
}
.accord .accord-title {
  background-color: #cccccc;
  font-size: 12px;
  padding: 5px 10px;
  font-family: Avenir;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.accord .accord-title .icon {
  line-height: 1;
  font-size: 10px;
}

.card {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 10px 20px;
  cursor: pointer;
}
.card:hover {
  background-color: #f6f6f6;
}
.card .card-body {
  margin-left: 14px;
}
.card .card-title {
  font-size: 16px;
  font-family: Avenir;
}
.card .position {
  font-family: Avenir;
  font-size: 9px;
}
.card .location {
  font-family: Avenir;
  font-size: 9px;
  color: #6b75ca;
}

.card-img {
  object-fit: cover;
}

@media (max-width: 1024px) {
  .card .location {
    font-size: 10px;
  }
  .card .card-title {
    font-size: 14px;
  }
  .card .position {
    font-size: 10px;
  }
  .card {
    padding: 10px;
  }
}

.home {
  background: #ffff;
  margin: 18px 18px 18px 18px;
  border-radius: 15px;
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.03);
  padding: 29px;
}

.greeting-img {
  text-align: center;
}

.greeting-img img {
  object-fit: cover;
}

.title {
  font-family: AvenirBold;
  font-size: 40px;
  font-weight: 900;
  text-align: center;
  color: #000000;
  margin: 18px 0 13px;
  letter-spacing: 1.67px;
}
.sub-title {
  text-align: center;
  letter-spacing: 0.71px;
  font-family: Avenir;
  font-size: 17px;
}

.sub-title .bold {
  font-weight: bold;
}

.second-text {
  color: #6b75ca;
}

.home-connect span {
  margin-right: 5px;
}

.btn-wrapper {
  text-align: center;
  margin: 23px 0;
}

.para {
  width: 80%;
  margin: 0 auto 34px;
}

.para p {
  font-family: Avenir;
  text-align: center;
  font-size: 12px;
  letter-spacing: 0.5px;
  margin-bottom: 16px;
}

.para p:last-child {
  margin: 0;
}

.report {
  font-family: Lato;
  font-size: 10px;
  text-align: center;
}

.list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 75%;
  margin: 0 auto 29px;
}

.list li {
  flex-basis: 50%;
  text-align: center;
}

.list li.list-title {
  font-family: Avenir;
  font-size: 18px;
  font-weight: 900;
  margin-bottom: 9px;
}

.list li.list-sub {
  font-family: Avenir;
  font-size: 12px;
}

.list li.list-sub a {
  color: #000;
}

@media (max-width: 1200px) {
  .greeting-img img {
    width: 150px !important;
    height: 150px !important;
  }
  .title {
    font-size: 36px;
  }
}
@media (max-width: 1024px) {
  .greeting-img img {
    width: 125px !important;
    height: 125px !important;
  }
  .title {
    font-size: 32px;
  }
}
@media (max-width: 576px) {
  .title {
    font-size: 28px;
    letter-spacing: 1.2px;
    margin: 15px 0 10px;
  }
  .sub-title {
    font-size: 16px;
  }
  .greeting-img img {
    width: 100px !important;
    height: 100px !important;
  }
  .home {
    padding: 20px;
  }
}

@media (max-width: 480px) {
  .home {
    margin: 10px;
    border-radius: 7px;
  }
  .list {
    flex-direction: column;
  }

  .list li:nth-child(3) {
    order: 2;
    margin-bottom: 15px;
  }

  .list li:nth-child(2) {
    order: 3;
  }

  .list li:nth-child(4) {
    order: 4;
  }
}

/* font imports  */
@font-face {
  font-family: Avenir;
  src: url(/zivost/static/media/AvenirLTStd-Book.8acb4de0.woff2);
}
@font-face {
  font-family: AvenirBold;
  src: url(/zivost/static/media/AvenirLTStd-Medium.2bd52a72.woff2);
}

/* resets */
*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
}

body {
  background: #eff1f7;
  /* border: 1px solid #ebebeb; */
}

